.Header {
  overflow: hidden;
  background-color: white;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-template-areas: "vjc vjc ham" "line1 line1 line1" "nba code code" "line2 line2 line2";
  text-align: center;
  align-items: center;
  padding: 0% 0%;
}

#HeaderLineThree {
  grid-area: line3;
  display: none;
}

.contactus {
  padding-top: 10px;
}

.HeaderLinkTabOne {
  grid-area: tab1;
  display: none;
  margin: auto 0%;
}

.AdmissionImage {
  width: 300px;
  height: 300px;
  overflow: hidden;
  position: relative;
  border-radius: 20px;
  /*Elastic box to center content area*/
  display: flex;
  align-items: center;
  justify-content: center;
}

.animcontent {
  padding-top: 10px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  width: 250px;
  height: 50px;
  border-radius: 10px;
  background: linear-gradient(-45deg, #790514, #790514, #448de0, #790514);
  background-size: 200% 200%;
  -webkit-animation: anim 4s ease infinite;
  animation: anim 4s ease infinite;
}

.animcontent:hover {
  animation: normal;
}

@keyframes anim {
  0% {
    background-position: 0% 50%;
    color: #fff;
  }
  50% {
    background-position: 100% 50%;
    color: rgba(255, 255, 255, 0.253);
  }
  100% {
    background-position: 0% 50%;
    color: #fff;
  }
}

@keyframes fade {
  0%, 100% {
    opacity: 0
  }
  50% {
    opacity: 1
  }
}

.HeaderLinkTabTwo {
  grid-area: tab2;
  display: none;
  padding-top: 2%;
  background-color: #790514;
  padding-bottom: 2%;
  vertical-align: center;
  align-items: center;
}

#HeaderVJCLogo {
  grid-area: vjc;
  padding-top: 10px;
}

#HeaderHamburger {
  grid-area: ham;
  padding-left: 50%;
}

#HeaderLineOne {
  grid-area: line1;
}

#HeaderNBALogo {
  grid-area: nba;
  padding-top: 5px;
  margin: 0% auto;
}

#HeaderVJCCode {
  grid-area: code;
}

#HeaderLineTwo {
  grid-area: line2;
}

.Header .HeadingFive h5 {
  margin: 0px;
  padding: 0px;
}

@media (min-width: 560px) {
  #HeaderNBALogo {
    padding-left: 80px;
  }
  #HeaderVJCCode {
    padding-left: 80px;
  }
}

@media (min-width: 768px) {
  .HeaderLinkTabOne {
    display: grid;
    grid-auto-flow: column;
    grid-row-gap: 0px;
  }
  .HeaderLinkTabTwo {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 14.285714% 85.714284%;
    grid-template-areas: "admission allothers";
  }
  .HeaderLinkTabAdmissionsOpen {
    grid-area: admission;
    display: grid;
    grid-template-columns: 100%;
    grid-template-areas: "top" "bottom";
  }
  #AdmissionsOpen {
    grid-area: top;
    justify-self: end;
    padding-top: 10px;
    margin: -10px;
    align-self: end;
  }
  #AdmissionsOpen img {
    padding: 0px;
    margin: 0px;
  }
  #HeaderLink4 {
    grid-area: bottom;
    padding: 0px;
    margin: 0px;
  }
  .HeaderLinkTabTwoGrouped {
    grid-area: allothers;
    display: grid;
    grid-auto-flow: column;
    align-items: end;
  }
  #HeaderLineThree {
    display: block;
    padding: 10px 0px;
  }
  #HeaderLineTwo {
    padding: 0px 10px 0px 0px;
  }
  #HeaderLineOne {
    padding: 0px 0px 10px 0px;
  }
  /*grid-template-columns: 38% 62% ;*/
  .Header {
    grid-template-columns: 38% 62%;
    grid-template-areas: "vjc nba" "line1 line1" "code tab1" "line2 line2" "tab2 tab2" "line3 line3";
  }
  #HeaderHamburger {
    display: none;
  }
  #HeaderVJCLogo {
    justify-self: start;
    padding-left: 15%;
  }
  #HeaderNBALogo {
    justify-self: end;
    padding-right: 15%;
    padding-top: 2%;
  }
  #HeaderVJCCode {
    justify-self: start;
  }
}

@media (min-width: 1024px) {
  .Header {
    grid-template-columns: 28% 12% 8% 52%;
    grid-template-areas: "vjc nba code tab1" "line1 line1 line1 line1" "tab2 tab2 tab2 tab2" "line2 line2 line2 line2";
  }
  #HeaderLineThree {
    display: none;
  }
  #AdmissionsOpen {
    justify-self: end;
    padding-right: 20%;
  }
  #HeaderNBALogo {
    justify-self: center;
    padding: 0px;
    padding-top: 7%;
  }
  #HeaderVJCCode {
    justify-self: start;
    padding: 0px;
  }
  .HeaderLinkTabOne {
    padding-left: 0%;
  }
  #HeaderLineOne {
    padding: 0px;
  }
  .HeaderLinkTabTwo {
    padding: auto 0;
  }
}

@media (min-width: 1440px) {
  .Header {
    grid-template-columns: 26% 8% 4% 62%;
  }
  .HeaderLinkTabTwo {
    padding: 0% 18%;
    padding-top: 1%;
  }
  .HeaderLinkTabOne {
    padding-left: 0%;
  }
  #HeaderNBALogo {
    justify-self: start;
  }
  .HeaderLinkTabTwo {
    grid-area: tab2;
    align-items: center;
    padding-bottom: 1%;
  }
}

@media (max-width:1080px) {
  .animcontent {
    font-size: 18px;
    width: 225px;
    height: 45px;
  }
  #HeaderVJCLogo {
    width: 280px;
  }
}

@media (max-width: 1024px) {
  .HeaderLinkTabOne {
    grid-area: unset;
  }
}

@media (max-width: 480px) {
  #HeaderVJCLogo {
    width: 180px;
  }
}